// by dribehance <dribehance.kksdapp.com>
angular.module("Youyi").factory("weixinServices", function($http, $route, $timeout, $rootScope, $location, toastServices, $window, oauthServices, localStorageService, config) {
    if (!$rootScope.wx_browser) return {};
    oauthServices.initWeixin($location.absUrl().split("#")[0]).then(function(data) {
        if (data.code == config.request.SUCCESS && data.status == config.response.SUCCESS) {
            $rootScope.timestamp = data.Result.item3.timestamp;
            $rootScope.nonceStr = data.Result.item3.nonceStr;
            wx.config({
                debug: false, // ,apialertpclogpc
                appId: config.weixin.appid, // 
                timestamp: $rootScope.timestamp, // 
                nonceStr: $rootScope.nonceStr, // 
                signature: data.Result.item3.signature, // 1
                jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage", "onMenuShareQQ", "onMenuShareWeibo", "onMenuShareQZone", "chooseWXPay"] // JSJS2
            });
        }
    });
    wx.ready(function() {
        // alert("initWeixinShareEvent")
        initWeixinShareEvent();
        // configreadyconfigconfigreadyready
    });
    wx.error(function(res) {
        // configerrorconfigdebugresSPA
    });

    function initWeixinShareEvent(title, link, thumbnail, desc) {
        wx.onMenuShareTimeline({
            title: title || config.share.title, // 
            link: link || config.share.link, // 
            imgUrl: thumbnail || config.share.thumbnail, // 
            trigger: function() {
                // alert("")
            },
            success: function() {
                // 
            },
            cancel: function() {
                // 
            }
        });
        wx.onMenuShareAppMessage({
            title: title || config.share.title, // 
            desc: desc || config.share.desc, // 
            link: link || config.share.link, // 
            imgUrl: thumbnail || config.share.thumbnail, // 
            type: "link", // ,musicvideolinklink
            dataUrl: "", // typemusicvideo
            success: function() {
                // 
            },
            cancel: function() {
                // 
            }
        });
        wx.onMenuShareQQ({
            title: title || config.share.title, // 
            desc: desc || config.share.desc, // 
            link: link || config.share.link, // 
            imgUrl: thumbnail || config.share.thumbnail, // 
            success: function() {
                // 
            },
            cancel: function() {
                // 
            }
        });
        wx.onMenuShareWeibo({
            title: title || config.share.title, // 
            desc: desc || config.share.desc, // 
            link: link || config.share.link, // 
            imgUrl: thumbnail || config.share.thumbnail, // 
            success: function() {
                // 
            },
            cancel: function() {
                // 
            }
        });
        wx.onMenuShareQZone({
            title: title || config.share.title, // 
            desc: desc || config.share.desc, // 
            link: link || config.share.link, // 
            imgUrl: thumbnail || config.share.thumbnail, // 
            success: function() {
                // 
            },
            cancel: function() {
                // 
            }
        });
    }
    return {
        // share
        initWeixinShareEvent: function(title, link, thumbnail, desc) {
            initWeixinShareEvent(title, link, thumbnail, desc);
        },
        // login
        queryAuthorizationCode: function() {
            var url = config.weixin.base_url + "?" + "appid=" + config.weixin.appid + "&redirect_uri=" + encodeURIComponent(config.weixin.redirect_uri) + "&response_type=" + config.weixin.response_type + "&scope=" + config.weixin.scope + "&state=" + config.weixin.state + config.weixin.wechat_redirect;
            $window.location.href = url;
        },
        queryAccessToken: function(code) {
            // var url = config.weixin.access_token_url + "?" + "appid=" + config.weixin.appid + "&secret=" + config.weixin.secret + "&code=" + code + "&grant_type=" + "authorization_code";
            // $window.location.href = url;
            var url = config.weixin.access_token_url + "?appid=" + config.weixin.appid + "&secret=" + config.weixin.secret + "&code=" + code + "&grant_type=authorization_code";
            return $http({
                // by dribehance <dribehance.kksdapp.com>
                url: url,
                method: "GET",
            }).then(function(data) {
                return data.data;
            });
        },
        queryUserinfo: function(input) {
            return $http({
                // by dribehance <dribehance.kksdapp.com>
                url: config.weixin.userinfo_url,
                method: "GET",
                params: angular.extend({}, {
                    "access_token": input.access_token,
                    "openid": input.openid,
                    "lang": "zh_CN"
                })
            }).then(function(data) {
                return data.data;
            });
        },
        prepare_pay: function(payment) {
            var url = config.weixin.base_url + "?" + "appid=" + config.weixin.appid + "&redirect_uri=" + encodeURIComponent(config.weixin.payment_redirect_uri) + "&response_type=" + config.weixin.response_type + "&scope=" + config.weixin.scope + "&state=" + JSON.stringify(payment) + config.weixin.wechat_redirect;
            $window.location.href = url;
        },
        // payment
        pay: function(payment) {
            toastServices.show();
            wx.chooseWXPay({
                // "appId": payment.appId,//config.weixin.appid,
                "timestamp": payment.timestamp, // jssdktimestamptimeStampS
                "nonceStr": payment.nonceStr, //  32 
                "package": payment.package_web, // prepay_idprepay_id=***
                "signType": payment.signType, // 'SHA1''MD5'
                "paySign": payment.paySign, // 
                success: function(res) {
                    // 
                    toastServices.hide();
                    // alert(JSON.stringify(res))
                    // alert("$location.path('tasks').replace();");
                    // $location.path("tasks").replace();
                    $timeout(function() {
                        $location.path("tasks").replace()
                    }, 1000)
                },
                cancel: function() {
                    toastServices.hide();
                    // alert("cancel");
                    $timeout(function() {
                        $location.path("tasks").replace()
                    }, 1000)
                },
                fail: function(res) {
                    toastServices.hide();
                    $timeout(function() {
                        $location.path("tasks").replace()
                    }, 1000)
                    // alert("chooseWXPay fail");
                    // alert(JSON.stringify(res));
                    // $rootScope.back();
                }
            });
        }
    }
});
//https://api.weixin.qq.com/sns/oauth2/access_token?appid=APPID&secret=SECRET&code=CODE&grant_type=authorization_code
